.feedback {
  position: fixed;
  z-index: 20;
  display: none;
  justify-content: end;
  align-items: start;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0; }

.feedback a {
  text-decoration: none;
  color: #ff3e79; }

.more__feedback {
  box-shadow: 0 0 0 100vw white;
  background-color: #fff;
  width: 320px;
  height: 100%;
  border-left: 1px solid #b8ffec;
  transition: all 0.5s ease 0s; }

.close__feedback {
  margin: 24px;
  margin-left: 16px; }

.feedback-form {
  background-color: #fff;
  width: 246px;
  margin-left: 24px; }

.feedback-form input {
  height: 46px;
  width: 276px;
  border: 1px solid #eaeaea;
  border-radius: 8px;
  padding: 15px;
  margin-bottom: 16px;
  opacity: 0.6; }

.feedback-form textarea {
  border: 1px solid #eaeaea;
  border-radius: 8px;
  width: 276px;
  height: 100px;
  margin-bottom: 16px;
  padding: 15px;
  padding-top: 10px;
  opacity: 0.6; }

.feedback-form__title {
  font-family: 'TTLakes-Bold';
  font-size: 24px;
  color: #333;
  margin-bottom: 20px;
  margin-top: 0; }

.feedback-form .deal {
  font-family: 'TTLakes-Medium';
  font-size: 0.875rem;
  line-height: 1rem;
  margin: 0 0 19px;
  color: #7e7e82; }

.submit-btn {
  display: flex;
  justify-content: start; }

.call-container {
  position: fixed;
  z-index: 20;
  display: none;
  justify-content: end;
  align-items: start;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0; }

.panel-container {
  box-shadow: 0 0 0 100vw white;
  background-color: #fff;
  width: 320px;
  height: 100%;
  border-left: 1px solid #b8ffec;
  transition: all 0.5s ease 0s; }

.close__product {
  margin-top: 24px;
  margin-left: 16px; }

.product-form {
  background-color: #fff;
  width: 246px;
  margin-left: 24px; }

.product-form__text {
  font-family: "TTLakes-Bold";
  letter-spacing: -0.6px;
  margin: 10px 0 24px;
  padding-top: 6px;
  color: #1b1c21; }

.product-form input {
  height: 46px;
  width: 276px;
  border: 1px solid #eaeaea;
  border-radius: 8px;
  padding: 15px;
  margin-bottom: 16px;
  opacity: 0.6; }

.product-form .deal {
  font-family: 'TTLakes-Medium';
  font-size: 0.875rem;
  line-height: 1rem;
  margin: 0 0 19px;
  color: #7e7e82; }

.product-form a {
  text-decoration: none;
  color: #ff3e79; }

.button-close {
  display: none;
  padding-left: 24px; }
