.body-page {
  font-family: "TTLakes-Regular", sans-serif;
  max-width: 1100px;
  height: 100%;
  margin: 0 auto;
  overflow-x: hidden; }

.header {
  background-color: white;
  padding: 19px 16px; }

.header-image {
  display: flex;
  align-items: center; }

.header-image__bt {
  display: flex;
  border: none;
  border-radius: 50%;
  background-color: white;
  margin-right: 16px; }

.header-image__bt:last-child {
  margin-right: 0; }

.header-image__logo {
  margin-right: auto; }

.header-image__bt--hidden {
  display: none; }

.services-section__nav-list {
  display: flex; }

.services-section__nav-item {
  padding: 8px 16px;
  flex-shrink: 0;
  white-space: nowrap; }

.services-section__nav-item:hover {
  outline: 2px solid #B8FFEC;
  outline-offset: -4px;
  border-radius: 6px; }

.services-section__nav {
  overflow-x: auto;
  scroll-behavior: smooth;
  margin-left: 16px; }

.services-section__nav::-webkit-scrollbar {
  height: 0; }

.services-section__button-container {
  display: none; }

.services-section__nav-link {
  text-decoration: none;
  color: inherit;
  font-size: 16px; }

.services-section {
  padding: 24px 0; }

.services-section__title {
  font-family: "TTLakes-Bold";
  margin-bottom: 24px;
  font-size: 28px;
  line-height: 40px;
  padding-left: 16px;
  color: #1B1C21;
  position: relative; }

.services-section__title::before {
  position: absolute;
  content: '';
  display: block;
  height: 40px;
  width: 8px;
  border-radius: 4px;
  background-color: #41f6d7;
  left: -4px; }

.description-section {
  margin-left: 16px;
  margin-right: 16px;
  margin-bottom: 16px; }

.description-section__text--additional {
  display: none; }

.description-section__text {
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.2px;
  color: #1B1C21;
  padding-left: 15px; }

.button {
  display: flex;
  border: none;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  margin-bottom: 40px;
  padding-left: 22px; }

.button:hover {
  cursor: pointer; }

.logo-container__image {
  margin-top: 16px;
  border-radius: 6px; }

.main {
  background-color: #F8F8F8; }
